export default {
  name: 'VueSwiper',
  data () {
    return {
      selected: 'Technologies',
      openSelect: false,
      slides: [1, 1, 1, 1, 1],
      swiperOptionSlider: {
        centeredSlides: true,
        spaceBetween: 10,
        slidesPerView :100,
      },
      swiperOption: {
        spaceBetween: 10,
        slidesPerView :'auto',
        pagination: {
          el: '.swiper-pagination',
          type: "bullets",
          clickable: true,
          renderBullet: function (index, className) {
            return '<span class="' + className + '">' + (index + 1) + "</span>";
          },
        },
        navigation: {
          nextEl: '.arrows__btn_next',
          prevEl: '.arrows__btn_prev'
        }
      }
    }
  },
  props: ['text']
}
